
      #app[data-booting] {
        visibility: hidden; /* Hide the app while its booting */
        width: 100%;
        height: 100%;
        /* While app is booting, layout might be broken, prevent scrollbar with overflow hidden */
        overflow: hidden;
      }

      #splashscreen {
        position: fixed;
        z-index: 1000;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: #030713;
        color: #a9a8a8;
        display: flex;
        flex-direction: column;
        align-items: center;
        opacity: 0;
        animation-name: splashin;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
      }
      @keyframes splashin {
        from {
          opacity: 0;
          /* transform: scale(0); */
        }

        to {
          opacity: 1;
          /* transform: scale(1); */
        }
      }
      #splashscreen_logo {
        margin: 2em;
      }
      #splashscreen_brand {
        font-size: 2em;
        color: orange;
      }
      #splashscreen_message {
        display: flex;
        justify-content: center;
        flex: 1;
        width: 80%;
        max-width: 40em;
        margin: 2em;
      }
      #booting_error {
        display: block;
        text-align: center;
        width: 100%;
      }
      #booting_error p {
        display: inline-block;
        color: #ba3939;
        background: #ffe0e0;
        border: 1px solid #a33a3a;
        padding: 1em 3em;
      }
      #booting_error details {
        display: inline-block;
        max-width: 100%;
      }
      #booting_error details summary {
        text-align: center;
      }
      #booting_error details pre {
        text-align: left;
        overflow: auto;
      }

      #splashscreen[data-booting-done] {
        animation-delay: 0;
        animation-duration: 0.3s;
        animation-name: splashout;
        animation-fill-mode: forwards;
      }
      @keyframes splashout {
        from {
          opacity: 1;
        }

        to {
          opacity: 0;
          display: none;
        }
      }
    